.navbar {
  display: grid;
  // align-items: center;
  text-align: center;
  flex-flow: column wrap;
  justify-content: center;
  position: fixed;
  background-color: #292b2a;
  width: 5rem;
  height: 100%;
  border-top-right-radius: 3%;

  &__nav {
    list-style-type: none;
    padding: 0;
    margin: 0;
    margin-top: 5%;
    display: flex;
    flex-flow: column wrap;
    align-content: center;
  }

  &__item {
    width: 100%;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2 ease-in-out;
    margin-top: 0.5rem;

    &-userName {
      margin-bottom: 1rem;
    }
  }

  &__item:last-child {
    margin-top: auto;
    display: flex;
    flex-direction: column;
    align-items: center;

    a {
      color: #c43f3f;
    }
  }

  &__status {
    margin-bottom: 1rem;
    justify-self: center;
  }
}

.fa-color a {
  color: rgb(84, 114, 75);
}
