.profile {
  &__userImg {
    width: 8rem;
    border-radius: 50%;
  }

  &__inner {
    padding: 0.5rem;
  }

  &__info {
    display: flex;

    &-header {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &-body {
      margin-left: 1rem;
      align-self: flex-end;
    }

    &-status {
      padding: 0.3rem;
      border: 1px solid rgba(68, 67, 67, 0.3);
      border-radius: 0.3rem;
      margin-right: 0.5rem;
    }
  }

  &__posts {
    margin-top: 1rem;
    height: fit-content;

    &-addPost textarea {
      resize: none;
      height: 0.3rem;
    }

    &-input {
      margin-bottom: 0.5rem;
    }
  }

  &__post {
    display: flex;
    flex-flow: column wrap;
    width: 95%;
    margin-top: 0.5rem;
    padding: 0.3rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);

    &-header {
      display: flex;
    }

    &-image {
      width: 3rem;
      border-radius: 50%;
    }

    &-name {
      align-self: center;
      margin-left: 0.4rem;
    }

    &-body {
      margin-top: 0.5rem;
    }
  }
}

.empty_status {
  color: rgba(0, 0, 0, 0.3);
  margin-right: 0.5rem;
}
