.login {
  width: 20rem;
  height: 30vh;
  z-index: 2;

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(87, 87, 87);
  }

  &__header {
    text-align: center;

    &h2 {
      font-weight: normal;
      font-size: 28px;
      line-height: 33px;
    }

    &-subtitle {
      font-weight: 300;
      font-size: 18px;
      line-height: 21px;
      color: rgba(0, 0, 0, 0.7);
      letter-spacing: 0.1px;
    }
  }

  &__body {
    display: flex;
    flex-direction: column;

    &-email {
      margin-bottom: 8px;
    }
    &-password {
      margin-bottom: 15px;
    }
  }

  &__footer {
    display: flex;
    flex-direction: column;
    margin-top: 0.4rem;

    &-signUp {
      margin-top: 5px;
      color: rgba(0, 0, 0, 0.5);
    }
  }

  &__callNotification {
    margin: 0.5rem;
  }
}
