.user {
  display: flex;
  width: 100%;
  height: 5rem;
  padding: 0.5rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  background-color: rgb(223, 219, 219);
  transition: all 0.2s ease-in-out;
  margin-bottom: 0.1rem;

  &:hover {
    background-color: rgb(204, 197, 197);
  }

  &__image {
    width: 5rem;
    cursor: pointer;
  }

  &__body {
    display: flex;
    flex-direction: column;
    margin-left: 0.5rem;
    flex: 1;
  }

  &__footer {
    align-self: flex-end;
  }
}

.users {
  padding: 0.3rem;

  &__pagination {
    margin-bottom: 0.5rem;
    border: 1px solid rgba(0, 0, 0, 0.3);
    width: fit-content;
    padding: 0.2rem;
    border-radius: 5%;
  }
}
